import { AnalysisResult } from '../common/AnalysisResult';
import { Sensitivity } from '../common/Sensitivity';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/* 
   Sensor anomaly information
   This data model is used by the inference process of Anomaly Detection 
*/
export class SensorAnomalyInfo {
 
    // Sensor Id 
    public sensorId: string;
 
    // List of results (model + file path) 
    public results: AnalysisResult[];
 
    // Sensitivity (for Anomaly) 
    public sensitivity: Sensitivity;
 
    // Object types: PERSON | VEHICLE | MOTORCYCLE ... 
    public objectTypes: ObjectTypeCode[];
 
    constructor(sensorId?: string, results?: AnalysisResult[], sensitivity?: Sensitivity, objectTypes?: ObjectTypeCode[]) { 
        this.sensorId = sensorId;
        this.results = results;
        this.sensitivity = sensitivity;
        this.objectTypes = objectTypes;
    }

}


